import Sidebar from "./sidebar.js";
import Landing from "./landing.js";
import GenerativePortfolio from "./generativePortfolio.js";
import Music from "./music.js";


function SidebarIfBrowser() {

    if (isBrowser) {
        return <Sidebar />
    }
    else {
        return null
    }
}


function Main() {
    return (
        <div >
            <div>
                <Landing style={{ height: "100vh" }} />
                <GenerativePortfolio style={{ height: "100vh" }} />
                <Music />

            </div>
            <SidebarIfBrowser />
        </div>
    );
}

export default Main;
