import React from "react";
import styled from 'styled-components';
// import tensen from "../images/gardenArtist2.png";
import tensenIm from "../images/dawn_chorus_1.png";
import springwalk from "../images/springwalk.png";

import { Button, Item, Title } from "./textConstants";
import SpotifyPlayer from 'react-spotify-player';



var imDict = {
  "Tensen Park": tensenIm,
  "swc": springwalk
};
var imArray = [tensenIm, springwalk]
var counter = 0

class Music extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showNames: true,
      nameActive: 0,
      round: this.props.round,
      image: imDict["swc"]
    };
    
  }

  ProcessClick(event){
    const id = event.target.id;
    var image = imDict[id]
    this.setState({showNames: false, image: image})

  }

  CloseArtist(event){
    this.setState({showNames: true})
  }
  // backgroundImage: "url(" + this.state.image + ")",

  render() {
    return (
      <div id="music">
        <section  style={{backgroundColor:"lightgray",marginLeft:"0%", marginTop:"0%",backgroundSize: "100%"}}>
          <Title>
            Music - Tensen Park
          </Title>  

          <Item style={{fontSize: "1.2em",textAlign:"left",margin:"5%"}}>              
              Nothing can move me to interesting mental spaces like music can.
              I love this quote by Aldous Huxley:
            </Item>
            <Item style={{fontSize: "1.2em",textAlign:"left",margin:"10%"}}>
              <i>
                After silence, that which comes nearest to expressing the inexpressible is music.
              </i>
            </Item>
            <Item style={{fontSize: "1.2em",textAlign:"left",margin:"5%"}}>
              Operating from Amsterdam, I'm experimenting with ambient music, and audio-visual works.
              My project is called Tensen Park.
              To get some <i>alive sounds</i>. use Field Recordings / generative and computation methods /
              old analog tape equipment. You can find me on <a href="https://tensenpark.bandcamp.com/">Bandcamp</a>, and <a href="https://open.spotify.com/artist/1lB15Q7MjR8s2j7TzeMP9Y?si=1tY8FRfMSzmNkhHO2nLPyg">Spotify</a>.
            </Item>
          
          <SpotifyPlayer
                  uri="https://open.spotify.com/playlist/2LRfRHlLHKHAQWiaIzxStJ?si=3214cd24929c4a91"
                  size={{
                      width: '50%',
                      height: 300,
                    }}
                  view={'coverart'}
                  theme={'white'}
                  />
            
         </section>
      </div>
    );
  }
}
export default Music













